import React, { useState, useEffect } from 'react'
import { useStyles } from './SubscriptionStyle';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CircleIcon from '@mui/icons-material/Circle';
import Button from '@mui/material/Button';
import axios from 'axios';
import { Grid, List, ListItem, Typography } from '@material-ui/core';
import Loading from '../../components/loading/Loading';

const Subscription = (props) => {
  const classes = useStyles();
  let unmounted = false;

  const [isPageLoading, setisPageLoading] = useState(false);
  const [isPageLoadingFailed, setisPageLoadingFailed] = useState(false);
  const [productDetails, setProductDetails] = useState({});

  useEffect(() => {
    getAllProducts();
  }, [unmounted]);

  const getAllProducts = async () => {
    setisPageLoading(true);
    unmounted = false;
    const source = axios.CancelToken.source();
    await axios.get(`${process.env.REACT_APP_SERVER}/products`)
      .then((response) => {
        setProductDetails(response.data.data);
      })
      .catch((error) => {
        if (!unmounted) {

        }
        setisPageLoadingFailed(true);
      })
      .finally(() => {
        setisPageLoading(false);
        return function () {
          unmounted = true;
          source.cancel("Cancelling in cleanup");
        };
      });
  }

  if (isPageLoadingFailed) {
    return (
      <Box style={{ paddingTop: '50%', backgroundColor: 'white' }}>
        <Loading isPageLoading={isPageLoading} />
      </Box>
    )
  }
  else if (isPageLoading) {
    return (
      <Box style={{ top: '50%' }}>
        <Loading isPageLoading={isPageLoading} />
      </Box>
    )
  }
  else {
    return (
      <Box className={classes.box}>
        <Card variant="outlined" style={{ height: '83vh' }}>
          <Typography variant='h4' className={classes.font} style={{ textAlign: 'center', margin: '20px 20px 0px 20px' }}>
            Explore the right plan for your Instuition
          </Typography>
          <Typography variant='h6' className={classes.font} style={{ textAlign: 'center', margin: '0px 20px 20px 20px' }}>
            Choose a plan that suits you the best!
          </Typography>
          <div style={{ margin: 0 }}>
            <Grid container className={classes.gridContainer}>
              <Grid item className={classes.grid}>
                <Card variant='elevation' elevation={10} className={classes.card}>
                  <Typography variant='h5' className={classes.font} style={{ textAlign: 'center', backgroundColor: '#c0c0c07a' }}>
                    {productDetails[0]?.product?.name}
                  </Typography>
                  <CardContent className={classes.font} style={{ textAlign: 'center' }}>
                    <Typography variant='h4'>
                      ₹{productDetails[0]?.unit_amount / 100}/year
                    </Typography>
                    <Typography variant='h6'>
                    <List sx={{ listStyleType: 'disc' }}>
                      {
                        productDetails[0]?.product?.metadata?.Features?.split(';').map((feature,index) => (
                          <ListItem key={index} sx={{ display: 'list-item' }}>
                          <CircleIcon style={{ paddingRight: 10 }} />{feature}
                        </ListItem>
                        ))
                      }
                      </List>
                    </Typography>
                  </CardContent>
                  <CardActions style={{ position: 'fixed', bottom: '110px', margin: '10% 10% 0% 6%' }}>
                    <Button variant='outlined' disabled = {props.currentProduct === productDetails[0]?.product?.name} size="medium" onClick={() => { props.onActions(2,productDetails[0]) }}>Choose the Plan</Button>
                  </CardActions>
                </Card>
              </Grid>
              <Grid item className={classes.grid}>
                <Card variant='elevation' elevation={10} className={classes.card}>
                  <Typography variant='h5' className={classes.font} style={{ textAlign: 'center', backgroundColor: '#c0c0c07a' }}>
                    {productDetails[1]?.product?.name}
                  </Typography>
                  <CardContent className={classes.font} style={{ textAlign: 'center' }}>
                    <Typography variant='h4'>
                      ₹{productDetails[1]?.unit_amount / 100}/year
                    </Typography>
                    <Typography variant='h6'>
                      <List sx={{ listStyleType: 'disc' }}>
                      {
                        productDetails[1]?.product?.metadata?.Features?.split(';').map((feature,index) => (
                          <ListItem key={index} sx={{ display: 'list-item' }}>
                          <CircleIcon style={{ paddingRight: 10 }} />{feature}
                        </ListItem>
                        ))
                      }
                      </List>
                    </Typography>
                  </CardContent>
                  <CardActions style={{ position: 'fixed', bottom: '110px', margin: '10% 10% 0% 6%' }}>
                    <Button variant='outlined' size="medium" disabled = {props.currentProduct === productDetails[1]?.product?.name} onClick={() => { props.onActions(2,productDetails[1])}}>Choose the Plan</Button>
                  </CardActions>
                </Card> 
              </Grid>
              <Grid item className={classes.grid}>
                <Card variant='elevation' elevation={10} className={classes.card}>
                  <Typography variant='h5' className={classes.font} style={{ textAlign: 'center', backgroundColor: '#c0c0c07a' }}>
                    {productDetails[2]?.product?.name}
                  </Typography>
                  <CardContent className={classes.font} style={{ textAlign: 'center' }}>
                    <Typography variant='h4'>
                      ₹{productDetails[2]?.unit_amount / 100}/year
                    </Typography>
                    <Typography variant='h6'>
                      <List sx={{ listStyleType: 'disc' }}>
                      {
                        productDetails[2]?.product?.metadata?.Features?.split(';').map((feature,index) => (
                          <ListItem key={index} sx={{ display: 'list-item' }}>
                          <CircleIcon style={{ paddingRight: 10 }} />{feature}
                        </ListItem>
                        ))
                      } 
                      </List>
                    </Typography>
                  </CardContent>
                  <CardActions style={{ position: 'fixed', bottom: '110px', margin: '10% 10% 0% 6%' }}>
                    <Button variant='outlined' size="medium" disabled = {props.currentProduct === productDetails[2]?.product?.name} onClick={() => {props.onActions(2,productDetails[2]) }}>Choose the Plan</Button>
                  </CardActions>
                </Card>
              </Grid>
            </Grid>
          </div>
          <CardActions style={{margin: "5px auto", alignItems:"center",justifyContent:"center"}}>
          <Button variant='contained' size="medium" onClick={() => {props.onActions(1,{})}}>{props.currentProduct!=='' ? 'Close' : 'Previous'}</Button>
        </CardActions>
        </Card>
      </Box>
    )

  }
}

export default Subscription